.socialLinksUL {
    display: flex;
    position: absolute;
    width: 400px;
    top: 80%;
    left: 50%;
    margin-left: -200px;
    /* transform: translate(-50%, -50%); */
  }
  
.socialLinksUL li {
list-style: none;
}
  
  .socialLinksUL li a {
    width: 50px;
    height: 50px;
    background-color: rgb(194, 192, 192);
    text-align: center;
    display: flex;
    align-content: center;
    justify-content: center ;
    line-height: 55px;
    font-size: 30px;
    margin: 0 10px;
    display: block;
    border-radius: 50%;
    position: relative;
    overflow: hidden;
    border: 3px solid rgb(190, 190, 190);
    z-index: 1;
    transition: 0.6s ease all;
  }
  
  .socialLinksUL li a .icon {
    position: relative;
    /* position: absolute; */
    color: #262626;
    transition: .5s;
    z-index: 3;
  }
  .socialLinksUL li a:hover{
    transition: 0.5s ease all;
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    background:rgb(214, 145, 55);
    border-color: #262626;
  }
  .socialLinksUL li a:hover .icon {
    /* color: rgb(194, 192, 192); */
    color: #262626;
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
    transition: 1s ease all;
    /* transform: scale(1.5); */
  }
  
  .socialLinksUL li a:before {
    content: "";
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgb(104, 74, 40);
    transition: .5s;
    z-index: 2;
  }
  
  .socialLinksUL li a:hover:before {
    top: 0;
    background:rgb(214, 145, 55);
  }
  
  
